<template>
    <div id="photo">
        <img :src="touxiang" alt="" @click="uploadImg"  class="touxiang">
        <input @change="shangchuan" type="file" ref="upload" accept="image/*" class="hiddenInput">
    </div>
</template>

<script>
export default {
    data(){
        return {
            touxiang:"",
            num:1
        }
    },
    methods:{
        uploadImg(){
            this.$refs.upload.click();
        },
        shangchuan(){
            console.log("...");
            let file = this.$refs.upload.files[0];
            console.log(file);
            //   Form    name 
            let data = new FormData() ;  //    
            data.append("avatar",file);
            this.$axios({
                url:"/vue/uploadAvatar",
                method:"POST",
                data:data,
                contentType:false,
                processData:false,
            }).then(res=>{
                //  this.touxiang = res.data.pic.replace(/public/,'');   //  
                this.touxiang = res.data.pic.replace(/public/,'http://123.57.62.191:7000/');  // 
                // localStorage.avatar = this.touxiang;
            })

            // processData: false,//data() "application/x-www-form-urlencoded" DOM  false
            // contentType: false,//jquery  JQuery data:formData,
        }
    },
    mounted(){
        // if(localStorage.avatar){
        //     this.touxiang = localStorage.avatar;
        // }else{
        //     //  
        //     this.touxiang = require("@/assets/images/photo.png");
        // }
        let num = "";
        if(sessionStorage.userInfo){
            num = JSON.parse(sessionStorage.userInfo).num;
        }
        if(num){
            this.$axios.post("/vue/getAvatar",{
                num
            }).then(res=>{
                  if(!!res.data.type){
                      this.touxiang = res.data.avatar.replace(/public/,'http://123.57.62.191:7000/');
                      console.log(this.touxiang);
                  }else{
                      this.touxiang = require("@/assets/photo.png");
                  }
            })
        }else{
            this.touxiang = require("@/assets/photo.png");
        }
    }
}
</script>

<style scoped>
.touxiang{
    width:3rem;
    height:3rem;
    border-radius: 50%;
}
.hiddenInput{
    display: none;
}
</style>