<template>
  <div class="r_step1">
    <div class="top">
      <h2></h2>
      <span></span>
      <span></span>
      <span></span>

      <span></span>
      <van-cell-group>
        <van-field v-model="mobile" placeholder="" :error-message="error" />
      </van-cell-group>
    </div>

    <van-button type="info" v-bind:disabled="flag" :to="{name:'r_step2'}"></van-button>
  </div>
</template>

<script>
export default {
  name: "r_step1",
  data() {
    return {
      mobile: "",
      flag: true,
      error: ""
    };
  },
  watch: {
    mobile(newval, oldval) {
      this.error="";
      var reg = /^1(3|4|5|6|7|8|9)\d{9}$/gi;
      if (reg.test(newval)) {
        sessionStorage.mobile=this.mobile;
        this.$axios
          .post("/vue/register_phone", {
            mobile: this.mobile
          })
          .then(res => {
            if (!!res.data.type) {
              this.flag = false;
            } else {
              this.error=""
              this.flag = true;
            }
          });
      } else {
        this.flag = true;
      }
    }
  }
};
</script>

<style scoped>
.top {
  margin-left: 2em;
}
.top > h2 {
  margin-bottom: 0;
}
.top > span {
  font-size: 0.5em;
}
.van-cell {
  width: 80%;
  border-bottom: 1px solid rgb(194, 189, 189);
  padding-left: 0 !important;
}

.van-button {
  display: block !important;
  margin: 0 auto !important;
  margin-top: 2em !important;
  height: 35px !important;
  line-height: 33px !important;
  width: 80%;
}
</style>

