<template>
  <div class="register">
    <van-nav-bar left-arrow @click-left="onClickLeft" />
    <van-steps :active="active">
      <van-step></van-step>
      <van-step></van-step>
      <van-step></van-step>
      <van-step></van-step>
    </van-steps>
    <router-view />
  </div>
</template>

<script>
export default {
  name: "register",
  data() {
    return {
      active: 0,
      step: this.$route.path.split("/")[2]
    };
  },
  // watch: {
  //   step: {
  //     deep: true,
  //     handler(newVal) {
  //       if (newVal == "r_step1") {
  //         active = 0;
  //       } else if (newVal == "r_step2") {
  //         active = 1;
  //       } else if (newVal == "r_step3") {
  //         active = 2;
  //       } else if (this.step == "r_step4") {
  //         this.active = 3;
  //       }
  //     }
  //   }
  // },

  methods: {
    onClickLeft() {
      this.$router.go(-1);
    }
  },
  mounted() {
    if (this.step == "r_step1") {
      this.active = 0;
    } else if (this.step == "r_step2") {
      this.active = 1;
    } else if (this.step == "r_step3") {
      this.active = 2;
    } else if(this.step == "r_step4"){
        this.active = 3;
    }
  }
};
</script>
