<template>
  <div class="r_step2">
    <div class="top">
      <h2></h2>
      <span></span>
      <span></span>
      <span></span>

      <span></span>
      <van-cell-group>
        <van-field v-model="incode" placeholder="" />
        <span class="code">{{code}}</span>
        <span class="tab" @click="tab"></span>
      </van-cell-group>
    </div>

    <van-button type="info" v-bind:disabled="flag" :to="{name:'r_step3'}"></van-button>
  </div>
</template>

<script>
export default {
  name: "r_step2",
  data() {
    return {
      incode: "",
      code: "AE86",
      flag: true
    };
  },
  watch:{
      incode(newVal,oldVal){
          if(newVal==this.code){
              this.flag=false;
              console.log(1)
          }else{
              this.flag=true;
          }
      }
  },
  methods:{
      tab(){
          this.code=parseInt(Math.random()*8999)+1000;
      }
  }
};
</script>

