    width: 80px;
    background-color: black;
    color:aliceblue;
}
.top {
  margin-left: 2em;
}
.top > h2 {
  margin-bottom: 0;
}
.top > span {
  font-size: 0.5em;
}
.van-cell {
  width: 60%;
