<template>
  <van-cell-group id="login">
    <div class="top">
      <svg class="icon" aria-hidden="true">
        <use xlink:href="#icon-QQ" />
      </svg>
      <span class="qq">QQ</span>
    </div>

    <van-field v-model="num" label="QQ" placeholder="QQ" />
    <van-field type="password" v-model="password" label="" placeholder="" :error-message="emsg"/>
    <div class="go">
      <svg class="icon" aria-hidden="true" @click="sendLogin">
        <use xlink:href="#icon-youjiantou" />
      </svg>
    </div>
    <div class="bottom">
      <div class="bottom_top">
        <span></span>
        <span class="last" @click="toRegister"></span>
      </div>
      <div class="bottom_bottom">
        <span class="first"></span>
        <span></span>
      </div>
    </div>
  </van-cell-group>
</template>

<script>
export default {
  name: "login",
  data() {
    return {
      num: this.$route.params.num,
      password: "",
      emsg:""
    };
  },
  watch:{
      num(newVal,oldVal){
         this.emsg=""
      },
      password(newVal,oldVal){
         this.emsg=""
      }
  },
  methods: {
    sendLogin() {
      this.$axios.post("/vue/login", {
        num:this.num,
        password:this.password
      }).then(res => {
        console.log(res.data.type);
        if (!!res.data.type) {  
          sessionStorage.userInfo = JSON.stringify({
            // token: res.data.token,
            num: this.num,
            nickname:res.data.nickname
          });
          console.log(res.data.type);
          console.log(1)
          this.$router.push({ name: "news" });
        } else {
          this.emsg=""
          sessionStorage.userInfo = JSON.stringify({
            token: ""
          });
        }
      });
    },
    toRegister(){
      this.$router.push({ name: "r_step1" });
    },
    mounted(){
      console.log(this.num)
    }

  }
};
</script>
<style type="text/css" scoped>
* {
  padding: 0;
  margin: 0;
}
.icon {
  width: 3em;
  height: 3em;
  vertical-align: -0.15em;
  fill: currentColor;
  overflow: hidden;
}
.qq {
  display: inline-block;
  font-size: 2rem;
  margin-left: 0.1em;
}
.top {
  margin-top: 6em;
  text-align: center;
  margin-bottom: 2 em;
}
.van-cell {
  width: 80%;
  background-color: rgb(243, 243, 243);
  border-radius: 24px;
  margin: 0.5em 0 0.5em 10%;
}
.go {
  text-align: center;
  margin-top: 3.5em;
}
.bottom {
  width: 37.5%;
  margin: 0 auto;
  font-size: 0.2em;
  font-weight: 900;
  margin-top: 5em;
}
.bottom_top {
  margin-bottom: 0.5em;
}
.last {
  float: right;
  padding-left: 3em;
  border-left: 1px solid rgb(192, 183, 183);
}
.first {
  color: rgb(192, 183, 183);
}
</style>