<template>
  <div class="r_step3">
    <div class="top">
      <h2></h2>
      <span></span>
      <span></span>
      <span></span>
      <span></span>
      <van-cell-group>
        <van-field v-model="nick" placeholder="" />
      </van-cell-group>
    </div>
    <div class="top">
      <h2></h2>
      <van-cell-group>
        <van-field type="password" v-model="pwd" placeholder="" :error-message="error1" />
      </van-cell-group>
    </div>
    <div class="top">
      <h2></h2>
      <van-cell-group>
        <van-field type="password" v-model="cpwd" placeholder="" :error-message="error2" />
      </van-cell-group>
    </div>
    <van-button type="info" v-bind:disabled="flag" :to="{name:'r_step4',params:{num:number}}"></van-button>
  </div>
</template>

<script>
export default {
  name: "r_step3",
  data() {
    return {
      nick:"",
      phone: "",
      flag: true,
      pwd: "",
      cpwd: "",
      error1: "6-12",
      error2: "",
      mark: 0,
      number:null,
    };
  },
  watch: {
    pwd(newVal, oldVal) {
      var reg_1 = /^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{6,12}$/gi;
      if (reg_1.test(newVal)) {
        this.mark = 1;
      } else {
        this.mark = 0;
      }
    },
    cpwd(newVal, oldVal) {
      if (this.mark) {
        if (this.pwd == newVal) {
          this.flag = false;
          this.$axios
            .post("/vue/register", {
              mobile: sessionStorage.mobile,
              password:newVal,
              nickname:this.nick
            })
            .then(res => {
              if (!!res.data.type) {
                this.flag = false;
                this.number=res.data.num;
                console.log(res.data.num);
              } else {
                this.flag = true;
              }
            });
        } else {
          this.flag = true;
        }
      }
    }
  }
};
</script>

<style scoped>
.top {
  margin-left: 2em;
}
.top > h2 {
  margin-bottom: 0;
}
.top > span {
  font-size: 0.5em;
}
.van-cell {
  width: 80%;
  border-bottom: 1px solid rgb(194, 189, 189);
  padding-left: 0 !important;
}

.van-button {
  display: block !important;
  margin: 0 auto !important;
  margin-top: 2em !important;
  height: 35px !important;
  line-height: 33px !important;
  width: 80%;
}
</style>