import Vue from 'vue'
import Router from 'vue-router'
import Index from './views/index.vue'
import News from './components/news.vue'
import Frends from './components/frends.vue'
import Tv from './components/tv.vue'
import Record from './components/record.vue'
import Shopping from './components/shopping.vue'
import Login from './views/login.vue'
import Register from './views/register.vue'
import R_step1 from './components/r_step1.vue'
import R_step2 from './components/r_step2.vue'
import R_step3 from './components/r_step3.vue'
import R_step4 from './components/r_step4.vue'
import Detail from './components/detail.vue'
import Hero from './components/hero.vue'
Vue.use(Router)

export default new Router({
  routes: [{
      path: "/",
      name: "login",
      component: Login
    },
    {
      path: "/login",
      name: "login",
      component: Login
    },

    {
      path: '/register',
      name: "register",
      component: Register,
      children: [{
          path: "r_step1",
          name: "r_step1",
          component: R_step1
        },
        {
          path: "r_step2",
          name: "r_step2",
          component: R_step2
        },
        {
          path: "r_step3",
          name: "r_step3",
          component: R_step3
        },
        {
          path: "r_step4",
          name: "r_step4",
          component: R_step4
        }
      ]

    },
    {
      path: '/index',
      name: 'index',
      component: Index,
      children: [

        {
          path: "news",
          name: "news",
          component: News
        },
        {
          path: "frends",
          name: "frends",
          component: Frends
        },
        {
          path: "tv",
          name: "tv",
          component: Tv
        },
        {
          path: "record",
          name: "record",
          component: Record
        },
        {
          path: "shopping",
          name: "shopping",
          component: Shopping
        },
        {
          path: "detail",
          name: "detail",
          component: Detail
        },
        {
          path: "hero",
          name: "hero",
          component: Hero
        }
      ]
    },
    {
      path: '**',
      redirect: {
        name: 'login'
      }
    },
  ]
})