      function formatterDateTime() {
        var date = new Date();
        var month = date.getMonth() + 1;
        var datetime =
          date.getFullYear() +
          "" + // ""
          (month >= 10 ? month : "0" + month) +
          "" + // ""
          (date.getDate() < 10 ? "0" + date.getDate() : date.getDate()) +
          "" +
          (date.getHours() < 10 ? "0" + date.getHours() : date.getHours()) +
          "" +
          (date.getMinutes() < 10
            ? "0" + date.getMinutes()
            : date.getMinutes()) +
          "" +
          (date.getSeconds() < 10
            ? "0" + date.getSeconds()
            : date.getSeconds());
        return datetime;
      }
      var a = this.keyword;
      var b = this.memery;
      $.ajax({
