import axios from "axios";
axios.defaults.baseURL = "http://123.57.62.191:7000/";
let AUTH_TOKEN = "";  
axios.defaults.headers.common['num'] = AUTH_TOKEN;    // token 
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded';


import {Toast} from "vant"; 

function fail(msg){
    Toast.clear();
    Toast({
        message:msg,
        icon: 'cross',
        duration:800
    });
}

 
import router from "@/router"

//     Interceptors 
//    
axios.interceptors.request.use(function (config) {
    //  
    
    if(sessionStorage.userInfo){
        AUTH_TOKEN = JSON.parse(sessionStorage.userInfo).num;
    }
    config.headers['num'] = AUTH_TOKEN;

    // Toast.loading({
    //     message: '...',
    //     duration:0.1
    // });

    return config;
}, function (error) {
    //   
    fail('-');
    return Promise.reject(error);
});

//   
axios.interceptors.response.use(function (response) {
    //  
   
    // setTimeout(()=>{
    //     Toast.clear();
    //     Toast.success({
    //         message:response.data.msg,
    //         duration:400
    //     });
        
    // },800)
    return response;
}, function (error) {
    fail('-')
    //   
    return Promise.reject(error);
});



export default axios;