<template>
  <div class="wrap">
    <van-sidebar v-model="activeKey">
      <van-sidebar-item v-for="(item,i) in all " :key="i" :title="item" @click="change(i)" />
    </van-sidebar>
    <div v-if="show[0]" class="c">
      <van-card
        v-for="(item,i) in heros"
        :key="i"
        @click="h_godet(i)"
        :desc="item.nikename"
        :thumb="item.simg"
      />
    </div>
    <div v-if="show[1]" class="c">
      <van-card
        v-for="(item,i) in aci"
        :key="i"
        @click="c_godet(i)"
        :desc="item.nikename"
        :thumb="item.simg"
      />
    </div>
    <div v-if="show[2]" class="c">
      <van-card
        v-for="(item,i) in azhan"
        :key="i"
        @click="z_godet(i)"
        :desc="item.nikename"
        :thumb="item.simg"
      />
    </div>
    <div v-if="show[3]" class="c">
      <van-card
        v-for="(item,i) in atan"
        :key="i"
        @click="t_godet(i)"
        :desc="item.nikename"
        :thumb="item.simg"
      />
    </div>
    <div v-if="show[4]" class="c">
      <van-card
        v-for="(item,i) in afa"
        :key="i"
        @click="f_godet(i)"
        :desc="item.nikename"
        :thumb="item.simg"
      />
    </div>
    <div v-if="show[5]" class="c">
      <van-card
        v-for="(item,i) in ashe"
        :key="i"
        @click="s_godet(i)"
        :desc="item.nikename"
        :thumb="item.simg"
      />
    </div>
    <div v-if="show[6]" class="c">
      <van-card
        v-for="(item,i) in afu"
        :key="i"
        @click="fu_godet(i)"
        :desc="item.nikename"
        :thumb="item.simg"
      />
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      activeKey: 0,
      all: ["", "", "", "", "", "", ""],
      show: [true, false, false, false, false, false, false],
      heros: [],
      aci:[],
      azhan:[],
      atan:[],
      afa:[],
      ashe:[],
      afu:[]
    };
  },
  methods: {
    h_godet(i){
      this.$router.push({ name: "hero", params: { h: this.heros[i] } });
    },
    c_godet(i){
      this.$router.push({ name: "hero", params: { h: this.aci[i] } });
    },
    z_godet(i){
      this.$router.push({ name: "hero", params: { h: this.azhan[i] } });
    },
    t_godet(i){
      this.$router.push({ name: "hero", params: { h: this.atan[i] } });
    },
    f_godet(i){
      this.$router.push({ name: "hero", params: { h: this.afa[i] } });
    },
    s_godet(i){
      this.$router.push({ name: "hero", params: { h: this.ashe[i] } });
    },
    fu_godet(i){
      this.$router.push({ name: "hero", params: { h: this.afu[i] } });
    },
    change(index) {
      this.show.map((item, i) => {
        if (i == index) {
          this.show.splice(i, 1, true);
        } else {
          this.show.splice(i, 1, false);
        }
        return item;
      });
      console.log(this.show);
    }
  },
  mounted() {
    this.$axios.post("/vue/hero", {}).then(res => {
      var ci=[];
      var zhan=[];
      var tan=[];
      var fa=[];
      var she=[];
      var fu=[];
      res.data.result.map((item, i) => {
        item.simg = require('@/assets/hero/Teemo.png')
        item.bimg = require('@/assets/hero/timo.jpg')
        if(item.type==""){
          ci.push(item)
        }else if(item.type==""){
          zhan.push(item)
        }else if(item.type==""){
          tan.push(item)
        }else if(item.type==""){
          fa.push(item)
        }else if(item.type==""){
          she.push(item)
        }else if(item.type==""){
          fu.push(item)
        }
      });
      this.aci=ci;
      this.azhan=zhan;
      this.atan=tan;
      this.afa=fa;
      this.ashe=she;
      this.afu=fu;
      this.heros = res.data.result;
      console.log(this.heros);
    });
  }
};
</script>

<style>
.van-sidebar {
  position:fixed !important;
  top: 0;
  left: 0;
  width: 20%;
}
.c {
  width: 75%;
  float: right;
}
.van-card__content {
  text-align: center;
}
</style>